body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body, html {
    width: 100%;
    height: 100%;
    overflow: auto;
    background-color: #0079be;
}

.App {
  /*text-align: center;*/
}

header {
  background-color: #0067a3;
  font-size: calc(10px + 2vmin);
  color: #fff;
  height: 8vmin;
  min-height: 40px;
}
header a {
  font-size: inherit;
  border: 0;
  background-color: #0067a3;
}

header a:nth-child(1) {
  float: left;
}
header a:nth-child(2) {
  font-size: calc(10px + 1vmin);
  float: right;
}

.goal-list-wrapper {
  display: flex;
  flex-wrap: wrap;
}
.create-goal-wrapper {
  overflow: hidden;
  padding:2%;
  color: white;
}
.goal-card {
  padding: 10px;
  margin: 1%;
  border-radius: 5px;
  background-color: #fff;
  color: #712e3e;
  width: 20%;
}
.create-goal-wrapper .goal-card {
  width:80%;
  margin-left: 0;
}
.create-goal-wrapper>div:nth-child(2){
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 1%;
}
.btn-wrapper {
  text-align: center;
  margin: 7% 0 2%;
}
button, a {
  border:0;
  background-color: #0079be;
  border-radius: 3px;
  padding: 7px 17px;
  margin: 0 2%;
  cursor: pointer;
  font-size: 14px;
  text-decoration: none;
  color: white;
  font-family: Arial;
  border: 1px solid white;
}
.goal-list-wrapper a {
  margin: 1% 2%;
  padding: 1%;
}
.goal-desc {
  height: 62px;
  overflow: auto;
}
textarea, input {
  width: 100%;
  box-sizing: border-box;
}
textarea {
  margin-top: 5px;
  height: 51px;
}
.back-btn {
  height:40px;
}
.back-btn a{
  margin-left:0;
}
.create-new {
  font-size: 50px;
  text-align: center;
}
.alignedgoals-wrapper {
	display: flex;
}
.alignedgoals-wrapper>div{
	margin:2%;
	width: 20%;
}
.agent-goals {
	margin-top: 2%;
}


.tip {
	position:relative;
	width:18px;
	height:25px;
	display: inline-block;
}
.tip>div {
	position:absolute;
	right:0;
	top:0;
	border-width:0 0 3px 3px;
	border-style: dashed;
	border-color: #d8d0d0;
	padding:6px 4px;
}
.tip div::before {
	position:absolute;
	content: "";
	border-width:6px;
	border-style: solid;
	border-color: transparent transparent #d8d0d0;
	bottom: 12px;
  right: 4px;
}
img {
	width: 25px;
	height: 25px;
}
.managergoal-desc, .agentgoal-desc {
	display: inline-block;
	width:calc(100% - 53px);
	margin-left: 10px;
	vertical-align: top;
	overflow: hidden;
  height: 21px;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.managergoal-desc {
	width:calc(100% - 35px);
	color: white;
}
.agentgoal-desc {
	width:calc(100% - 53px);
	color: antiquewhite;
}
